<script>
import { mapState } from "vuex";
import data from "@/db/data.json";
export default {
  data: () => ({
    items: data,
    currentVideo: 1,
    currentUrl: "",
    ScurrentUrl: "",
    currentName: "النطق بشكل صحيح",
    ScurrentName: "احتياج الطفل التكرار",
    ScurrentVideo: 5,
  }),
  mounted() {
    this.currentUrl = this.items.videos[0].src;
    this.ScurrentUrl = this.items.videos[4].src;
    // console.log(this.currentUrl);
  },
  methods: {
    changeVideoUrl(videoId) {
      // var loadTime =
      //   window.performance.timing.domContentLoadedEventEnd -
      //   window.performance.timing.navigationStart;
      this.currentUrl = videoId.src;
      // console.log(loadTime);
      this.currentVideo = videoId.id;
      this.currentName = videoId.title;
      // let video = document.querySelector(".main-embed");
      let video = document.querySelector(".col-main-embed");
      video.classList.add("active_video");
      setTimeout(() => {
        video.classList.remove("active_video");
      }, 250);
    },
    SchangeVideoUrl(videoId) {
      this.ScurrentUrl = videoId.src;
      this.ScurrentVideo = videoId.id;
      this.ScurrentName = videoId.title;
      let video = document.querySelector(".col-main-embed.second");
      video.classList.add("active_video");
      setTimeout(() => {
        video.classList.remove("active_video");
      }, 250);
    },
  },
  ...mapState({
    S_darkMode: (store) => store.port.S_darkMode,
    switchLang: (store) => store.port.switchLang,
  }),
};
</script>

