<script>
import { mapActions, mapState } from "vuex";
import data from "@/db/data.json";
import { getAuth, signOut } from "firebase/auth";
export default {
  data: () => ({
    drawer: false,
    drawerTwo: false,
    darkMode: false,
    currentBtn: 1,
    items: data,
    enLang: false,
    settings: false,
    select: "AR",
    langs: ["EN", "AR"],
    barScroll: true,
  }),
  computed: {
    ...mapState({
      isUserIn: (store) => store.port.isUserIn,
      S_darkMode: (store) => store.port.S_darkMode,
      switchLangVuex: (store) => store.port.switchLang,
    }),
    currentRouter() {
      return (
        this.$route.name == "contact" ||
        this.$route.name == "qrcode" ||
        this.$route.name == "about" ||
        this.$route.name == "home" ||
        this.$route.name == "services" ||
        this.$route.name == "qualification" ||
        this.$route.name == "metrics" ||
        this.$route.name == "advices" ||
        this.$route.name == "questions" ||
        this.$route.name == "articles" ||
        this.$route.name == "videos" ||
        this.$route.name == "who-are-we"
      );
    },
    homeRouter() {
      // console.log(this.$route.name === "home");
      return this.$route.name === "home";
    },
    contactNavBar() {
      return this.darkMode
        ? "white"
        : this.barScroll && !this.darkMode
        ? "black"
        : "";
    },
    ContactBar() {
      return this.$route.name == "contact" && !this.darkMode && this.barScroll
        ? true
        : false;
    },
  },
  methods: {
    ...mapActions(["A_isUserIn", "A_redirectTo"]),
    logout() {
      this.A_isUserIn(false);
      this.A_redirectTo("login");

      const auth = getAuth();
      signOut(auth)
        .then(() => {
          // Sign-out successful.
        })
        .catch((error) => {
          // An error happened.
          console.log(error);
        });
    },
    /*START::SWITCH_MODE */
    switchMode() {
      this.darkMode
        ? [
            (this.$vuetify.theme.dark = true),
            localStorage.setItem("darkMode", true),
            (this.$store.state.port.S_darkMode = true),
          ]
        : [
            (this.$vuetify.theme.dark = false),
            localStorage.setItem("darkMode", false),
            (this.$store.state.port.S_darkMode = false),
          ];
    },
    switchLang() {
      this.enLang
        ? [
            localStorage.setItem("enLang", true),
            (this.$store.state.port.switchLang = true),
            (this.$vuetify.rtl = false),
          ]
        : [
            localStorage.setItem("enLang", false),
            (this.$store.state.port.switchLang = false),
            (this.$vuetify.rtl = true),
          ];
    },
    scrollToSection(title) {
      let section = document.getElementById(`${title}`);
      if (
        section &&
        (title == "Home" ||
          title == "About" ||
          title == "Services" ||
          title == "Qualification")
      ) {
        scrollTo({
          behavior: "smooth",
          top: section.offsetTop,
        });
      } else {
        scrollTo({
          behavior: "smooth",
          top: 0,
        });
      }
    },
    scrollToContact(title) {
      title == "Contact" ? window.scrollTo(0, 0) : "";
    },
    scrollToSections(item) {
      item.title == item.title ? window.scrollTo(0, 0) : "";
    },
    activeLinkInScroll() {
      this.items.links.slice(0, 4).forEach((el) => {
        const SECTION = document.getElementById(el.title);
        if (window.scrollY > SECTION.offsetTop) {
          // console.log(el.title);
          this.currentBtn = el.id;
        }
      });
    },
  },
  mounted() {
    // this.activeLinkInScroll();
    this.$store.state.port.navHeight = Number.parseInt(
      this.$refs.nav.styles.height
    );
    if (localStorage.getItem("darkMode")) {
      this.$vuetify.theme.dark = JSON.parse(localStorage.getItem("darkMode"));
      this.darkMode = JSON.parse(localStorage.getItem("darkMode"));
      this.$store.state.port.S_darkMode = JSON.parse(
        localStorage.getItem("darkMode")
      );
    } else {
      this.darkMode = false;
      this.$store.state.port.S_darkMode = false;
      this.$vuetify.theme.dark = false;
    }
    if (localStorage.getItem("enLang")) {
      this.enLang = JSON.parse(localStorage.getItem("enLang"));
      this.$store.state.port.switchLang = JSON.parse(
        localStorage.getItem("enLang")
      );
      this.$vuetify.rtl = !JSON.parse(localStorage.getItem("enLang"));
    } else {
      this.enLang = false;
      this.$store.state.port.switchLang = false;
      this.$vuetify.rtl = true;
    }
    /*START:: */
    // if (window.scrollY == 0) {
    //   this.barScroll = true;
    // } else {
    //   this.barScroll = false;
    // }
    document.addEventListener("scroll", function () {
      if (window.scrollY == 0) {
        this.barScroll = true;
      } else {
        this.barScroll = false;
      }
      // console.log(this.barScroll);
    });

    //
    // let taskbar = document.querySelector(".the_nav_bar");
    // window.onscroll = () => {
    //   if (window.scrollY > 10) {
    //     taskbar.classList.add("the_primary");
    //   } else {
    //     taskbar.classList.remove("the_primary");
    //   }
    // };
    //
  },
  created() {},
};
</script>

