<template>
  <div class="" v-if="false">
    <div class="chat_bot" v-if="false">
      <v-scroll-x-transition>
        <v-card color="" v-if="chatBot">
          <v-card-text>
            <v-row class="">
              <v-col cols="8">
                <div class="col_content d-flex align-center">
                  <div class="icon">
                    <v-badge overlap dot bottom color="success">
                      <v-avatar color="primary" size="50">
                        <v-icon color="white">mdi-chat-outline</v-icon>
                      </v-avatar>
                    </v-badge>
                  </div>
                  <div class="text pl-3">
                    <v-card-title class="--text">ChatBot</v-card-title>
                    <v-card-subtitle class="grey--text">Online</v-card-subtitle>
                  </div>
                </div>
              </v-col>
              <v-col class="d-flex align-start justify-end">
                <v-btn @click="chatBot = false" fab small text>
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text
            class="scroll_section py-0"
            style="min-height: 250px; max-height: 250px; overflow-y: auto"
          >
            <!--  -->
            <v-list color="" shaped class="chat_scroll">
              <v-list-item
                v-for="(item, i) in newText"
                class=""
                :key="i"
                dense
                round
                :class="[
                  i % 2 == 0
                    ? 'primary user_box white--text my-3'
                    : 'white chat_box text-right',
                  darkMode && i % 2 !== 0
                    ? 'black--text'
                    : !darkMode && i % 2 !== 0
                    ? 'secondary white--text'
                    : '',
                ]"
              >
                <v-list-item-content>
                  <v-list-item-title>{{ item }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list>
            <!--  -->
            <div class="red"></div>
          </v-card-text>
          <v-card-text class="pb-0">
            <v-text-field
              @keydown.enter="sendMessage"
              solo
              v-model="message"
              background-color="secondary"
              dark
              @click:append="sendMessage"
              label="Type your message here"
              append-icon="mdi-send"
            ></v-text-field>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text class="text-center pt-0">
            Powered by ChatBot
          </v-card-text>
        </v-card>
      </v-scroll-x-transition>
    </div>
    <div class="sub_icon">
      <!-- <v-btn> -->
      <!-- </v-btn> -->
    </div>
    <div class="mini_chat" data-aos="fade-up-left" data-aos-duration="2000">
      <v-slide-y-transition>
        <v-btn
          class="include"
          v-if="chatBot === false"
          @click="chatBot = true"
          fab
          color="primary"
        >
          <v-icon color="">mdi-chat-outline</v-icon>
        </v-btn>
      </v-slide-y-transition>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({
    newText: [],
    message: "",
    botMsg: [],
    userMsg: [],
    botResult: "",
    chatBot: false,
  }),
  methods: {
    sendMessage() {
      this.$axios
        .get(
          `https://www.cleverbot.com/getreply?key=CC8uqcCcSO3VsRFvp5-uW5Nxvow&input=${this.message}`
        )
        .then((res) => {
          if (this.message) {
            this.newText.push(this.message, res.data.output);
          }
          this.message = "";
          let scroll_section = document.querySelector(".scroll_section");
          scroll_section.scrollTop = scroll_section.scrollHeight + 50;
          scroll_section.scrollBy(0, 10);
          // console.log(scroll_section.offsetTop);
          // scroll_section.scrollTo({
          //   behavior: "smooth",
          //   top: scroll_section.offsetTop,
          // });
        });
    },
    onClickOutsideStandard() {
      this.chatBot = false;
      // console.log("yes");
    },
    include() {
      return document.querySelector(".include");
    },
  },
  computed: {
    ...mapState({
      darkMode: (store) => store.port.S_darkMode,
    }),
  },
};
</script>

