.chat_bot {
  position: fixed;
  z-index: 2000;
  bottom: 0%;
  right: 3%;
  width: 325px;
}
.chat_box {
  width: 182px;
  margin-left: auto;
  border-radius: 20px;
  border-bottom-right-radius: 0px !important;
}
.user_box {
  width: 182px;
  margin-right: auto;
  border-radius: 20px;
  border-bottom-left-radius: 0px !important;
}
.mini_chat {
  position: fixed;
  bottom: 25px;
  z-index: 2;
  right: 25px;
}
