import router from "@/router";

export const port = {
  state: {
    isUserIn: false,
    navHeight: "",
    switchLang: true,
    S_darkMode: false,
  },
  getters: {},
  mutations: {
    isUserIn(state, payload) {
      state.isUserIn = payload;
    },
    redirectTo(state, payload) {
      router.push({ name: payload });
    },
  },
  actions: {
    A_isUserIn({ commit }, payload) {
      commit("isUserIn", payload);
    },
    A_redirectTo({ commit }, payload) {
      commit("redirectTo", payload);
    },
  },
};
