<script>
import data from "@/db/data.json";
import { mapState } from "vuex";
export default {
  data: () => ({
    items: data,
    scrollButton: false,
  }),
  mounted() {
    this.assignHomeHeight();
    let scrollBtn = document.querySelector(".scroll_to_top");
    scrollY > 700
      ? [(scrollBtn.style.display = "block"), (this.scrollButton = true)]
      : [(scrollBtn.style.display = "none"), (this.scrollButton = false)];

    window.onscroll = () => {
      scrollY > 700
        ? [(scrollBtn.style.display = "block"), (this.scrollButton = true)]
        : [(scrollBtn.style.display = "none"), (this.scrollButton = false)];
    };
  },
  methods: {
    assignHomeHeight() {
      // let navHeight= this.$store.state.port.navHeight
      // let sum = window.innerHeight;
      let home = document.getElementById("Home");
      // home.style.height = `${sum - 200}px`;
      home.style.height = `${650}px`;
    },
    scrollToAbout() {
      scrollTo({
        behavior: "smooth",
        top: document.getElementById("About").offsetTop,
      });
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
    scrollToSection(section) {
      this.$router.push({ name: section }).then(() =>
        window.scrollTo({
          behavior: "smooth",
          top: 0,
        })
      );
    },
  },
  computed: {
    ...mapState({
      switchLang: (store) => store.port.switchLang,
    }),
  },
};
</script>

